<template>
  <div class="bottom-bar">
    <div class="flex-row" v-for=" (item, index) in tabs" :key="item.type" @click="onChangeTab(item.type)">
      <div class="tab" :class="{'active': item.type === currentTab}">
        {{item.name}}
      </div>
      <div class="line" v-if="index !== tabs.length - 1"/>
    </div>
  </div>
</template>
<script>
// const TAB_TYPE_001 = [
//   {
//     name: '店铺首页',
//     type: 'tab-home',
//   },
//   {
//     name: '商品分类',
//     type: 'tab-category',
//   },
//   {
//     name: '预约报名',
//     type: 'tab-signUp',
//   },
// ];
const TAB_TYPE_NORMAL = [
  {
    name: '店铺首页',
    type: 'tab-home',
  },
  {
    name: '全部商品',
    type: 'tab-product',
  },
  {
    name: '商品分类',
    type: 'tab-category',
  },
];
/** 千店千面 店铺首页 底部tab */
export default {
  name: 'bottomBar',
  props: {
    currentTab: {
      type: String,
      default: 'tab-home',
    },
    storeId: {
      type: String,
      default: ''
    },
    // showSignUp: { // 001号店与三翼鸟店 显示预约报名
    //   type: Boolean,
    //   default: false
    // },
    storeCode: {
      type: String,
      default: ''
    },
    showSceneTab: {
      type: Boolean,
      default: false
    },
    storeSceneData: {
      type: Object,
      default: () => {}
    }
  },
  computed: {
    tabs() {
      let tabArr = [...TAB_TYPE_NORMAL]
      // if (this.showSignUp) {
      //   tabArr = [...TAB_TYPE_001]
      // }
      if (this.showSceneTab && this.storeSceneData?.storeSenceUrl) {
        tabArr.push(
          {
            name: '场景',
            type: 'tab-scene',
          }
        )
      }
      return tabArr
    }
  },
  methods: {
    onChangeTab(type) {
      if (type === 'tab-signUp') {
        if (this.$util.isUplus()) {
          this.$util.openNewWindow(`mpaas://homedesign?needAuthLogin=1&originShopCode=${this.storeCode}`, true, true);
        } else {
          this.$dialog.alert({
            message: '请下载海尔智家APP进行操作'
          });
        }
      } else if (type === 'tab-scene') {
        this.$store.dispatch('goPageByType', {
          type: this.storeSceneData.jumpType,
          params: this.storeSceneData.storeSenceUrl
        })
      } else {
        this.$emit('changeTab', type);
      }
    }
  },
}
</script>
<style lang="scss" scoped>
