  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 1rem;
  background: white;
  display: flex;
  flex-direction: row;
  align-items: center;
  z-index: 90;
  @include max-width;
}
.flex-row {
  display: flex;
  flex: 1;
  flex-direction: row;
  align-items: center;
}
.line {
  width: .02rem;
  height: .72rem;
  background: #eee;
}
.tab {
  flex: 1;
  font-size: .28rem;
  text-align: center;
  color: #333;
}
.tab.active {
  font-weight: bold;
}
