<template>
  <div class="tab-recommend">
    <!-- :background="isTabFixed ? 'rgba(0,0,0,0.7)': 'rgb(0,0,0,0)'" -->
    <van-sticky
      @scroll="onTabScroll"
      offset-top="1.1rem">
      <van-tabs
        v-model="activeTabIndex"
        ref="tabs"
        line-height=".06rem"
        line-width=".48rem"
        :sticky="false"
        :animated="false"
        :swipeable="false"
        title-active-color="white"
        title-inactive-color="white"
        :swipe-threshold="6"
        background="transparent"
        :before-change="beforeTabChange"
        color="white">
        <van-tab v-for="(item,index) in barList" :title="item" :key="index" >
        </van-tab>
      </van-tabs>
    </van-sticky>
    <keep-alive>
      <bar-selected
        v-if="barList[activeTabIndex] === '精选'"
        v-on:changeBar="onChangeBar"
        :storeId="storeId"
        :shareId="shareId"
        :storeSign="storeSign"
        :storeSceneData="storeSceneData"
        :modelList="modelList"
        :modelFetchend="modelFetchend"
        :class="{'tab-recommend-background':isTabFixed}"
      />
      <bar-product
        v-if="barList[activeTabIndex] === '商品'"
        :storeId="storeId"
        :shareId="shareId"
        :offsetTop="'1.62rem'"
      />
      <bar-activity
        v-if="barList[activeTabIndex] === '活动'"
        :storeId="storeId"
        :storeSign="storeSign"
      />
      <bar-update
        v-if="barList[activeTabIndex] === '上新'"
        :storeId="storeId"
      />
      <!-- <bar-scene
        v-if="barList[activeTabIndex] === '场景'"
        :storeId="storeId"
      /> -->
    </keep-alive>
    <div v-show="isTabFixed" class="pic-top-bg-cover" :style="{'background': storeSign.storeBgImg ? `url(${storeSign.storeBgImg}) 0% 0% / 100% 5.96rem no-repeat` : 'white'}">
      <div class="top-bg-masking" style="height: 1.66rem"/>
    </div>
  </div>
</template>
<script>
import {
  getSelectedModel,
  getShopPreview,
} from '@/http/service/shop';
// import barSelected from '../top-bar/selected/barSelected';
// import barActivity from '../top-bar/barActivity';
// import barScene from '../top-bar/barScene';
// import barUpdate from '../top-bar/barUpdate';
const barSelected = () => import('../top-bar/selected/barSelected');
const barActivity = () => import('../top-bar/barActivity');
const barProduct = () => import('./tabProduct');
// const barScene = () => import('../top-bar/barScene');
const barUpdate = () => import('../top-bar/barUpdate');

/** 千店千面 底部tab 店铺首页 */
export default {
  props: {
    storeSceneData: {
      type: Object,
      default: () => {}
    },
    storeId: {
      type: String,
      default: '',
    },
    shareId: {
      type: String,
      default: '',
    },
    storeSign: {
      type: Object,
      default: () => {},
    },
    isPreview: {
      type: Boolean,
      default: false,
    },
  },
  components: {
    barSelected,
    barActivity,
    barProduct,
    // barScene,
    barUpdate,
  },
  data() {
    return {
      activeTabIndex: 0,
      // barList: ['精选', '商品', '活动', '上新', '场景']
      isTabFixed: false,
      modelList: [],
      modelFetchend: false,
    };
  },
  mounted() {
    // if (this.barList[0] === '精选' && this.storeId) {
    //   this.getModels();
    // }
  },
  watch: {
    // storeSign: {
    //   handler(newVal) {
    //     console.log('店招:', newVal);
    //     if (!newVal.chosenTab && this.barList.includes('精选')) {
    //       this.barList = this.barList.slice(1, this.barList.length);
    //     } else if (this.storeId) {
    //       this.getModels();
    //     }
    //     if (newVal.newTab && !this.barList.includes('上新')) {
    //       this.barList.push('上新');
    //     }
    //     if (newVal.sceneTab && newVal.synStoreType !== '9' && this.storeSceneData?.storeSenceUrl && !this.barList.includes('场景')) {
    //       this.barList.push('场景');
    //     }
    //   },
    //   immediate: true,
    // },
    // location(newVal) {
    //   if (this.storeId) {
    //     this.getModels();
    //   }
    // },
  },
  computed: {
    barList() {
      let list = ['精选', '商品', '活动']
      if (!this.storeSign.chosenTab && list.includes('精选')) {
        list = list.slice(1, list.length);
      } else if (this.storeId && !this.modelList.length) {
        this.getModels();
      }
      if (this.storeSign.newTab && !list.includes('上新')) {
        list.push('上新');
      }
      if (this.storeSign.sceneTab && this.storeSign.synStoreType !== '9' && this.storeSceneData?.storeSenceUrl && !list.includes('场景')) {
        list.push('场景');
      }
      return list
    },
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  methods: {
    beforeTabChange(index) {
      this.$util.gioTrackWithVariable('MB15616', {
        store_topTab_name: this.barList[index] || 'null'
      })
      if (this.barList[index] === '场景') {
        // 跳转到三翼鸟店铺场景列表
        if (this.storeSceneData?.storeSenceUrl) {
          this.$store.dispatch('goPageByType', {
            type: this.storeSceneData.jumpType,
            params: this.$util.injectISOAddressCodeToURL(this.storeSceneData.storeSenceUrl, this.$store.getters.getlocationInfo)
          })
        }
        return false
      }
      return true
    },
    onTabScroll (scrollInfo) {
      if (this.isTabFixed !== scrollInfo.isFixed) {
        this.isTabFixed = scrollInfo.isFixed;
        console.log('onTabScroll-isFixed：', scrollInfo);
      }
      this.$emit('scroll', scrollInfo)
    },
    onChangeBar(index) {
      console.log('切换跳转：', index);
      this.activeTabIndex = index;
    },
    getModels() {
      console.log('是否预览：', this.isPreview);
      const params = {
        storeId: this.storeId,
        streetId: this.location.townshipId,
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        districtId: this.location.districtId,
        modelStatus: this.isPreview ? 0 : 1,
      };
      getSelectedModel(params).then(res => {
        console.log('精选模块：', res);
        if (res.success) {
          if (res.data && res.data.length) {
            res.data.sort((a, b) => a.modelSort - b.modelSort);
            this.modelList = [...res.data];
          }
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
        this.modelFetchend = true
      });
    },
    getPreViewModels() {
      const param = {
        // storeType: this.$route.query.storeType,
        storeCode: this.storeSign.storeCode,
      };
      getShopPreview(param).then(res => {
        if (res.success) {
          this.shopStoreId = res.data.controlViews.storeId;
          if (res.data.controlViews && res.data.controlViews.length) {
            res.data.controlViews.sort((a, b) => a.modelSort - b.modelSort);
            this.modelList = res.data.controlViews;
            this.modelList.map(item => {
              item.images = item.storeModelImgs;
            });
          }
        } else {
          this.$toast(res.message || '获取数据失败，请稍后重试');
        }
      });
    }
  },
}
</script>
