  position: relative;
  padding-top: .24rem;
  width: 100%;
  background: transparent;
}

.tab-recommend ::v-deep .van-tabs ::v-deep .van-hairline--top-bottom {
  background-color: transparent;
  width: 100%
}
.van-tabs ::v-deep .van-hairline--top-bottom::after {
  border-width: 0;
}
/* .tab-recommend ::v-deep .van-tabs {
  margin-top: .24rem;
} */
.tab-recommend ::v-deep .van-tabs__wrap {
  height: .52rem;
  border: 0;
  padding: 0;
  margin-bottom: .1rem;
}
.tab-recommend ::v-deep .van-tab__text {
  font-size: .32rem;
  font-weight: bold;
  margin-bottom: .08rem;
}
.tab-recommend ::v-deep .van-sticky--fixed {
  z-index: 2560;
  left: 0;
  right: 0;
  position: fixed;
  @include max-width;
}
