<template>
<!-- 手机浏览器出现高度塌陷，故多加一层div -->
  <div style="clear:both;">
    <div class="shop-header" v-if="storeSign.o2oStoreName || shopInfo.storeName">
      <div class="flex-row flex-align-center" style="margin-top: .34rem;">
        <img class="store-icon" v-if="storeSign.showLogo" @click="logoClick" :src="storeSign.logoUrl || 'https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/uplus_logo.png'"/>
        <div class="flex-column" style="position: relative; flex: 1;" @click="goShopInfo">
          <div class="flex-row flex-align-center" style="margin-bottom: .1rem">
            <div class="store-name">
              {{storeSign.o2oStoreName || storeSign.storeName}}
            </div>
            <span class="right-arrow"/>
          </div>
          <div class="flex-row flex-align-center">
            <div class="store-tag"><span>官方</span></div>
            <img
              class="star"
              v-if="storeSign.storeType === 6 || storeSign.shopStoreCategory === '12'"
              :src="`${$cdnImageUrl}/shop/icon_shop_star.png`"/>
            <div class="fans">
              粉丝数{{formatNumber(shopInfo.collectors)}}
            </div>
          </div>
        </div>
        <div class="collection flex-row flex-align-center" @click="changeCollection">
          <img :src="`${$cdnImageUrl}/shop/icon_shop_collection_new.png`" v-if="!shopInfo.isCollection"/>
          <span>{{shopInfo.isCollection ? '已关注' : '关注'}}</span>
        </div>
      </div>
      <div class="location flex-row flex-align-center"
        v-if="showAddress"
        @click="tooglePopup"
      >
        <div/>
        <span>{{storeSign.storeAddress}}</span>
        <img
          :src="showAddressPopup?
            'http://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_shop_more_address.png'
            : 'http://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/shop/icon_shop_navigation_new.png'"
        />
      </div>
      <div class="quality"/>
    </div>
  </div>
</template>
<script>
import { collectStore } from '@/http/service/shop'
import { notifyWashingMachine } from '@/http/service/goods'
import utils from '@/utils/utils'
/** 千店千面 店铺首页 头部 */
export default {
  props: {
    shopInfo: {
      type: Object,
      default: () => {},
    },
    storeSign: {
      type: Object,
      default: () => {},
    },
    isPreView: {
      type: Boolean,
      default: false,
    },
    hasSubStore: {
      type: Boolean,
      default: true,
    },
    taskId: {
      type: String,
      default: '',
    },
    stageId: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      isCollection: false,
      isUplus: utils.isUplus(),
      isSgRn: utils.isSgRn(),
      subStoreList: [], // 分店列表
    };
  },
  // watch: {
  //   storeId: {
  //     handler(newVal) {
  //       console.log('header-storeId=', newVal);
  //       this.getShopInfo();
  //     },
  //     immediate: true,
  //   }
  // },
  watch: {
    'shopInfo.isCollection': {
      handler(newVal) {
        if (newVal) {
          this.notifyWashingMachine()
        }
      },
      immediate: true,
    },
  },
  methods: {
    // 点击收藏，通知洗衣机
    notifyWashingMachine() {
      if (this.taskId && this.stageId) {
        notifyWashingMachine({
          taskId: this.taskId,
          stageId: this.stageId,
        }).then((res) => {
          if (res.success) {
            if (res.data) {
              console.log('success')
            }
          } else {
            console.log(res.message)
          }
        })
      }
    },
    changeCollection() {
      if (this.isPreView) {
        return;
      }
      // 判断登陆
      if (!this.$store.getters.getLoginStatus) {
        this.$store.dispatch('doLogin')
        return
      }
      collectStore({
        collectId: this.shopInfo.storeId,
        type: 1,
      }).then(res => {
        if (res.success) {
          if (res.data) {
            this.isCollection = res.totalCount === 1
            // 25093 埋点
            this.$util.gioTrackWithVariable('mall_store_click_follow', {
              actionType: this.shopInfo.isCollection ? '取关' : '关注'
            })
            // 重新获取粉丝数
            this.$emit('updateShopInfo');
          }
        } else {
          this.$toast(res.message)
        }
      });
    },
    goShopInfo() {
      this.$util.gioTrackWithVariable('mall_store_click_storeInfo', {
        storeId: this.shopInfo.storeId + ''
      })
      this.$router.push(`/shopInfo?storeId=${this.shopInfo.storeId}`);
    },
    logoClick() {
      this.$util.gioTrackWithVariable('mall_store_click_storeInfo', {
        storeId: this.shopInfo.storeId + ''
      })
    },
    tooglePopup() {
      if (this.showAddressPopup) {
        this.$emit('toogleModal', true)
      } else {
        this.goAddress(this.storeSign);
      }
    },
    formatNumber(number) {
      if (!number) {
        return 0;
      } else {
        if (number >= 10000) {
          return `${(number / 10000).toFixed(2)}万`;
        } else {
          return number;
        }
      }
    },
    goAddress(data) {
      const that = this;
      if (!data.latitude || !data.longitude) {
        return ''
      }
      const elat = data.latitude
      const elng = data.longitude
      const encode = encodeURI(`${elng},${elat}`)
      const name = data.o2oStoreName || data.storeName// encodeURI(data.name)
      if (this.$util.isSgRn()) {
        const lat = (elat + '').indexOf('.') !== -1 ? elat : elat.toFixed(6);
        const lng = (elng + '').indexOf('.') !== -1 ? elng : elng.toFixed(6);
        window.postMessage(JSON.stringify({
          type: 'goNewWebview',
          data: {
            newUrl: `https://uri.amap.com/marker?position=${encodeURI(`${lng},${lat}`)}&name=${name}`,
          },
        }));
      } else if (this.$util.isIos() || this.$util.isAndroid()) {
        // window.location = this.getAmapURL()
        setTimeout(function() {
          that.$util.openNewWindow(`https://uri.amap.com/marker?position=${encode}&name=${name}`, true);
        }, 500);
      } else {
        this.$util.openNewWindow(this.getAmapURL(data), true)
      }
    },
    getAmapURL(data) {
      if (!data) {
        return ''
      }
      const elat = data.latitude
      const elng = data.longitude
      if (!this.$util.isIos() && !this.$util.isAndroid()) {
        const encode = encodeURI(`${elng},${elat}`)
        const name = encodeURI(data.storeName)
        return this.$util.openNewWindow(`https://uri.amap.com/marker?position=${encode}&name=${name}`, true);
      }
      let proto = this.$util.isIos() ? 'iosamap://path' : this.$util.isAndroid ? 'amapuri://route/plan' : ''
      const AppUrl = proto + '?t=0&dlat=' + elat + '&dlon=' + elng + '&dname=' + encodeURI(data.storeName)
      return AppUrl
    },
  },
  computed: {
    // storeType  0-非旗舰店 1-旗舰店 2-售后店 3-生态店 4-品类店 5-品旗店 6-001号店
    // shopStoreCategory 店铺二级分类页面 01-乡镇店 11-智家店 12-三翼鸟店 13-综合店 31-生态店 32-家装 33-智能互联 34-安防 35-生活家电
    showAddress() {
      const storeSign = this.storeSign
      return storeSign.storeAddress && (['01', '11', '12', '13'].includes(storeSign.shopStoreCategory) || storeSign.storeType === 6 || storeSign.storeType === 1)
    },
    showAddressPopup() {
      const storeSign = this.storeSign
      return (['11', '13'].includes(storeSign.shopStoreCategory) || storeSign.storeType === 1) && this.hasSubStore
    }
  }
}
</script>
